<template>
  <div class="Picturepreview">
    <template v-if="!infoSuObjOff&&!chievementOff">
      <!-- 查询 -->
      <el-card class="main">
        <el-form :inline="true" :model="queryfrom" class="me_from demo-form-inline">
          <el-form-item>
            <el-select style="width:190px;" v-model="queryfrom.projectId" placeholder="请选择代理成交单项目">
              <el-option label="全部" value></el-option>
              <el-option
                v-for="(item,index) in $store.getters.projectAll"
                :key="item.projectId+index+'s'+item.projectName"
                :label="item.projectName"
                :value="item.projectId"
              ></el-option>
            </el-select>
          </el-form-item>
            <!-- <template v-if="adminFn($store.getters.userinfo.role)">
            <el-form-item label="成交">
            <el-select
              v-model="value"
              multiple
              filterable
              remote
              reserve-keyword
              @focus="remoteMethods"
              placeholder="请选择成交单项目"
              :remote-method="remoteMethod"
            >
              <el-option
                v-for="item in options"
                :key="item.projectId"
                :label="item.projectName"
                :value="item.projectId"
              ></el-option>
               </el-select>
               </el-form-item>
            </template> -->
          <el-form-item>
            <el-select style="width:190px;" v-model="queryfrom.status" placeholder="请选择状态">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="认购保存" :value="1"></el-option>
              <el-option label="认购提交" :value="2"></el-option>
              <el-option label="认购审核通过" :value="3"></el-option>
              <el-option label="认购审核拒绝" :value="4"></el-option>
              <el-option label="认购退定" :value="5"></el-option>
              <el-option label="业绩保存" :value="6"></el-option>
              <el-option label="业绩提交" :value="7"></el-option>
              <el-option label="业绩审核通过" :value="8"></el-option>
              <el-option label="业绩审核拒绝" :value="9"></el-option>
              <el-option label="业绩退房" :value="10"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-date-picker
              style="width:290px;"
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="选择开始时间"
              end-placeholder="选择结束时间"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-input style="width:190px;" v-model="queryfrom.keyWord" placeholder="客户名和房号"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="background:#0b8d7e;border:none;" @click="onSubmit">搜索</el-button>
            <el-button
              v-if="adminFn($store.getters.userinfo.role)"
              type="primary"
              style="background:#f1420ef7;border:none;"
              @click="AddPic"
            >添加成交单</el-button>
            <!-- <el-button
              type="primary"
              style="background:#f1420ef7;border:none;"
              @click="daiAddPic"
            >添加代理成交单</el-button> -->
          </el-form-item>
        </el-form>
      </el-card>
      <!-- 查询 -->
      <!-- 表格数据 -->
      <el-card style="margin-top:20px;">
        <header style="font-weight: 800;padding-bottom: 8px;color: #000;">成交单列表</header>
        <el-table
          ref="table"
          height="410"
          :cell-style="cellStyle"
          :header-cell-style="{background:'#edfaf8',color:'#333333'}"
          :row-class-name="tableRowClassName"
          :data="tableData"
          border
          style="width: 100%"
        >
          <el-table-column prop="customName" label="客户姓名" width="100"></el-table-column>
          <el-table-column prop="customerPhone" label="客户电话" width="120"></el-table-column>
          <el-table-column prop="tradeTime" width="180" label="认购日期"></el-table-column>
          <el-table-column prop="projectName" label="楼盘">
            <template slot-scope="scope">{{scope.row.projectName}}&nbsp;{{scope.row.roomNo}}</template>
          </el-table-column>
          <el-table-column prop="deptName" label="门店"></el-table-column>
          <el-table-column prop="statusStr" label="状态" width="130">
            <!-- <template
            slot-scope="scope"
            >{{scope.row.checkStatus===1?'认购保存':scope.row.checkStatus===2?'认购提交':scope.row.checkStatus===3?'认购审核通过':scope.row.checkStatus===4?'认购审核拒绝':scope.row.checkStatus===5?'认购退定(不可编辑)':scope.row.checkStatus===6?'认购退定(可编辑)':scope.row.checkStatus===7?'业绩提交':scope.row.checkStatus===8?'业绩审核通过':scope.row.checkStatus===9?'业绩审核拒绝':'业绩退房'}}</template>-->
          </el-table-column>
          <!-- <el-table-column prop="agentName"  width="100" label="经纪人"></el-table-column> -->
          <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">详情</el-button>
            </template>
          </el-table-column>
        </el-table>
        <Pagination
          @handleSizeChange="handleSizeChange"
          @handleCurrentChange="handleCurrentChange"
          :paginationData="paginationData"
        />
      </el-card>
      <!-- 表格数据 -->

      <!-- 成交单录入 -->
      <addPic type="成交单录入" :dialogVisible="addPicOff" />
      <!-- 成交单录入 -->
       <!-- 成交单录入 -->
      <!-- <addPic type="代理成交单录入" :dialogVisible="daiaddPicOff" /> -->
      <!-- 成交单录入 -->
    </template>
    <!-- 成交单详细 -->
    <infoSuccess
      type="成交单详细"
      @EidtPic="EidtPic"
      :edit="true"
      @InvoiceOffFN="infoSuObjOff=false"
      :infoSuObj="infoSuccessData"
      v-if="infoSuObjOff"
    />
    <!-- 成交单详细 -->
    <!-- 业绩单详细 -->
    <addchieve
      @InvoiceOffFN="chievementOff=false"
      :infoSuObj="infoSuccessData"
      v-if="chievementOff"
    />
    <!-- 业绩单详细 -->
    <!-- 编辑成交单 -->
    <editPic :infoSuccessData="infoSuccessData" ref="editPic" :dialogVisible="editPicOff" />
    <!-- 编辑成交单 -->
  </div>
</template>
<script>
import myMixin from "@/mixin/index";
import {
  tradeList,
  employerAllProject,
  agencyTicketList,
  
} from "@/api/business/Picturepreview";
import { ticketDetail } from "@/api/business/commission";
import { dateTime, adminFn } from "@/utils/common";
import infoSuccess from "@/components/common/infoSuccess";
import addPic from "@/components/business/Picturepreview/addPic";
import editPic from "@/components/business/Picturepreview/editPic";
import addchieve from "@/components/business/Picturepreview/addchieve";
export default {
  mixins: [myMixin],
  components: {
    infoSuccess,
    addPic,
    editPic,
    addchieve
  },
  data() {
    return {
      selctAll: [], //项目楼盘列表
    
      value1: "", //时间选择
      queryfrom: {
        //主页查询参数
        projectId: "",
        status: "",
        keyWord: "",
        pageNo: 1,
        pageSize: 10,
        beginTime: "",
        endTime: ""
      },
      paginationData: {
        pageSize: 10,
        currentPage: 1,
        total: 100
      },
      tableData: [
        // {
        //   ticketId: 2, //成交单ID
        //   customName: "客户姓名", //客户姓名
        //   preMobile: "", //客户手机号
        //   midMobile: "", //客户手机号
        //   sufMobile: "", //客户手机号
        //   projectId: 23, //楼盘ID
        //   projectName: "", //楼盘名字
        //   departmentId: 123, // 门店ID
        //   departmentName: "", //门店名字
        //   agentName: "", //经纪人姓名
        //   agentPhone: "18938766656", //经纪人电话
        //   agentId: "", //经纪认的UID
        //   tradeTime: "", //对应状态的时间
        //   checkTime: "", //审核时间
        //   commitTime: "", //提交时间
        //   checkStatus: 1, //1:已保存(默认状态)，2 :待审核 3:审核通过  4审核拒绝 5退订不可编辑 6退订可编辑
        //   payStatus: 1,
        //   roomNo: "房号",
        //   ticketStatus: 1, //总状态 0：初始化（默认状态） 1：成交信息已审核通过 2：业绩信息已审核通过
        //   checkStatus2: 1, //0未录入 1:已保存(默认状态)，2 :待审核 3:审核通过  4审核拒绝 5退房
        //   statusStr: "认购保存" //状态描述
        // }
      ],
      // 成交单详细
      infoSuccessData: null, //成交单详情
      infoSuObjOff: false,
      // 成交单详细
      addPicOff: {
        //  成交单录入
        trunoff: false
      },
      daiaddPicOff:{
        // 代理成交单录入
        trunoff:false
      },
      editPicOff: {
        //成交单编辑
        trunoff: false
      },
      //业绩单详情
      chievementOff: false,

      //业绩单详情
      
     
    };
  },
  methods: {
    adminFn,
    //第几页
    handleCurrentChange(val) {
      this.queryfrom.pageNo = val;
      this.paginationData.currentPage = val;
      this.requestLsit();
    },
    //一页条数
    handleSizeChange(val) {
      this.queryfrom.pageSize = val;
      this.paginationData.pageSize = val;
      this.requestLsit();
    },
    //数据请求
    requestLsit() {
      // if (adminFn(this.$store.getters.userinfo.role)) {
        // 成交单
        tradeList(this.queryfrom).then(res => {
          if (res.data.code === "000") {
            this.tableData = res.data.bizData.items.map(item => {
              item.tradeTime = dateTime(item.tradeTime);
              return item;
            });
            this.paginationData.total = res.data.bizData.total;
          }
        });
      // } else {
      //   // 代理成交单
      //   agencyTicketList(this.queryfrom).then(res => {
      //     if (res.data.code === "000") {
      //       this.tableData = res.data.bizData.items.map(item => {
      //         item.tradeTime = dateTime(item.createTime);
      //         return item;
      //       });
      //       this.paginationData.total = res.data.bizData.total;
      //     }
      //   });
      // }
    },
    //搜索
    onSubmit() {
      this.queryfrom.pageNo=1
      this.queryfrom.pageSize=10
      this.requestLsit();
    },
    //添加成交单
    AddPic() {
      this.addPicOff.trunoff = true;
    },
    //添加代理成交单
    // daiAddPic(){
    //   this.daiaddPicOff.trunoff=true
    // },
    //详情
    handleClick(item) {
       if(item==null||item==''){
         item=this.$store.getters.ticlkdeil
       }
      ticketDetail({ ticketId: item.ticketId }).then(res => {
        this.infoSuObjOff = true;
        if (res.data.code === "000") {
          res.data.bizData.statusStr = item.statusStr;
          // if (res.data.bizData.checkStatus2 == 1) {
          //   //判断是否业绩保存
          //   this.chievementOff = true;
          // } else {
            
          // }
          this.infoSuccessData = res.data.bizData;
          this.$store.dispatch("PicTickDieal", {data:res.data.bizData,item});
        }
      });
    },
    //成交单编辑
    EidtPic() {
      this.editPicOff.trunoff = true;
    },
  
    
  
  },
  mounted() {
    this.requestLsit();
  },
  watch: {
    value1: {
      handler() {
        if (this.value1 == null) {
          this.queryfrom.beginTime = "";
          this.queryfrom.endTime = "";
          return true;
        }
        this.queryfrom.beginTime = dateTime(this.value1[0]);
        this.queryfrom.endTime = dateTime(this.value1[1]);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.Picturepreview .main /deep/.el-card__body {
  padding-bottom: 0;
}
</style>